class HandlebarsTemplate {

  constructor() {
    this.template = $("#location-template");
    this.gallery = $('.location__gallery-interior');
    
    // Get the template script
    const theTemplateScript = this.template.html();
    // Compile the template
    const theTemplate = Handlebars.compile(theTemplateScript);
    // Pass our data to the template
    const theCompiledHtml = theTemplate(LOCATION_DATA);
    // Add the compiled html to the page
    this.gallery.html(theCompiledHtml);
  }
}

class Gallery {

  constructor() {
    this.location = $('.location');
    this.nextBtn = $('.location__gallery-btn--next');
		this.prevBtn = $('.location__gallery-btn--prev');
		this.mapBtn = $('.location__map-cta');
		this.activeClass = 'active';
		this.openClass = 'open';
    this.events();
  }

  goPrev() {
    if ($('.active').prev('.location').length){
      $('.active').removeClass(this.activeClass).prev('.location').addClass(this.activeClass);
    } else {
      $('.active').removeClass(this.activeClass);
      this.location.last().addClass(this.activeClass);
    }
  }

  goNext() {
    if ($('.active').next('.location').length){
      $('.active').removeClass(this.activeClass).next('.location').addClass(this.activeClass);
    } else {
      $('.active').removeClass(this.activeClass);
      this.location.first().addClass(this.activeClass);
    }
	}
	
  events() {
    this.location.each((index, item) => {
      if(index < 1) {
        $(item).addClass(this.activeClass);
      }
    });

    this.prevBtn.on('click', () => {
      this.goPrev();
    });
    
    this.nextBtn.on('click', () => {
      this.goNext();
		});
		
		this.mapBtn.on('click', (e) => {
			const targetBtn = $(e.target);
			const openText = targetBtn.data('open');
			const closeText = targetBtn.data('close');
			
			targetBtn.parent().toggleClass(this.openClass);
			
			if(targetBtn.parent().hasClass(this.openClass)) {
				targetBtn.text(closeText);
			} else {
				targetBtn.text(openText);
			}
    });
  }
}

$(() => {
  window.handlebars = new HandlebarsTemplate();
  window.gallery = new Gallery();
});
